
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState } from "vuex";
export default {
name: "topiclist",
props: ["name"],
components: {
Cell,
Group,
Scroller,
Tab,
TabItem
},
computed: {
...mapState(["topics"])
},
methods: {
routeto(index) {


this.$router.push("/topiclist/" + this.topics[index].name);
},

freshData() {

var app = this;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: 1,
limit: this.limit
}
})
.then(function(res) {
app.lists = res.data.data;

app.$refs.pullup.donePulldown();
});
},
loadMoreData() {
var app = this;
this.page = this.page + 1;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: this.page,
limit: this.limit
}
})
.then(function(res) {

for (var i = 0; i < res.data.data.length; i++) {

app.lists.push(res.data.data[i]);
}

app.$refs.pullup.donePullup();
});
}
},
data: function() {
return {
lists: [],
page: 1, //当前页数，默认显示第一页
limit: 10, //每页显示的数据
pullupConfig: {

content: "上拉加载更多",
downContent: "松开进行加载",
upContent: "上拉加载更多",
loadingContent: "加载中....."
},
pulldownConfig: {

content: "下拉加载更多",
downContent: "松开进行加载",
upContent: "下拉加载更多",
loadingContent: "加载中....."
}
};
},



beforeRouteEnter: function(to, from, next) {




next(function(vm) {

vm.$http
.get("/topics", {
params: {
tab: to.params.name, //动态获取参数获取
page: vm.page,
limit: vm.limit
}
})
.then(function(res) {

if (res.data.success) {
vm.lists = res.data.data;
}
});
});
},
beforeRouteUpdate: function(to, from, next) {

var app = this;
this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit
}
})
.then(function(res) {
if (res.data.success) {
app.lists = res.data.data;
}
});
next();
}
};



